.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Montserrat";
  position: relative;
  z-index: 1;
  text-align: center; }
  .title__back {
    font-size: 100px;
    color: #EEEFF0;
    font-weight: 800;
    line-height: 86px; }
  .title__front {
    color: #0B1C3D;
    font-size: 38px;
    font-weight: 500;
    position: absolute; }
  .title--contact .title__back {
    font-size: 70px; }
  .title--contact .title__front {
    font-size: 38px; }

@media only screen and (max-width: 991px) {
  .title__front {
    font-size: 32px; }
  .title__back {
    font-size: 75px; } }

@media only screen and (max-width: 767px) {
  .title__front {
    font-size: 21px; }
  .title__back {
    font-size: 50px; }
  .title--recruitment_title .title__back {
    line-height: 65px; }
  .title--recruitment_title .title__front {
    line-height: 35px; }
  .title--contact {
    display: none; } }

@media only screen and (max-width: 575px) {
  .title__back {
    font-size: 40px !important; }
  .title--recruitment_title .title__back {
    line-height: 53px; }
  .title--recruitment_title .title__front {
    line-height: 23px; } }

