.floating_footer {
  // position: fixed;
  // padding-top: 16px;
  // left: 0;
  bottom: 0;
  // height: 75px;
  top: auto;
  // width: 100%;
  background-color: rgba(123, 123, 123, 0.8);
  padding-left: 72px;
  padding-right: 16px;

  .grow {
    flex-grow: 1
  }

  .button {
      font-family: Usual;
      font-size: 14px;
      font-weight: 500;
      color: #FFFFFF;
      margin-left: 25px;
      height: 100%;
      min-width: 130px;
      border-color: #3AB77D;
      border-style: solid;
      border-width: 2px;
      background-color: #3AB77D;
      
      &:hover {
          background-color: #008650;
      }

      &.back {
        background-color: #3C394A;
        border-color: #3C394A;
        &:hover {
            background-color: #3C394A62;
        }
      }

      &.transparent {
        background-color: #3AB77D62;
        &:hover {
            background-color: #00865062;
        }
      }
      &.withcomment {
        color: #0F808E;
        background-color: #E6F5F6;
        border-color: #E6F5F6;
        &:hover {
            color: #FFFFFF;
            background-color: #3AB77D;
            border-color: #3AB77D;
        }
      }
      &.disapprove {
        color: #FD5451;
        background-color: #DDDFE3;
        border-color: #DDDFE3;
        &:hover {
            color: #FFFFFF;
            background-color: #3AB77D;
            border-color: #3AB77D;
        }
      }
  }
}